"use client"

import Link from 'next/link';
import { useSearchParams } from 'next/navigation';
import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from '@/components/ui/pagination'; // Assuming pagination is in ui
import { cn } from '@/lib/utils';
import { useMemo, forwardRef, ComponentProps } from 'react';

// We have to import React because of forwardRef
import * as React from 'react';

// Pagination component doesn't exist in shadcn, let's create a placeholder structure.
// This is not the full shadcn pagination, but a custom one using its style.

export function PaginationControls({
  currentPage,
  totalPages,
  className,
}: {
  currentPage: number;
  totalPages: number;
  className?: string;
}) {
  const searchParams = useSearchParams();

  const createPageURL = (pageNumber: number | string) => {
    const params = new URLSearchParams(searchParams);
    params.set('page', pageNumber.toString());
    return `?${params.toString()}`;
  };

  const paginationItems = useMemo(() => {
    const pages = [];
    const delta = 2;
    const left = currentPage - delta;
    const right = currentPage + delta + 1;
    const range = [];
    const rangeWithDots: (number | string)[] = [];
    let l: number | undefined;

    for (let i = 1; i <= totalPages; i++) {
      if (i === 1 || i === totalPages || (i >= left && i < right)) {
        range.push(i);
      }
    }

    for (const i of range) {
      if (l) {
        if (i - l === 2) {
          rangeWithDots.push(l + 1);
        } else if (i - l !== 1) {
          rangeWithDots.push('...');
        }
      }
      rangeWithDots.push(i);
      l = i;
    }

    return rangeWithDots;
  }, [currentPage, totalPages]);


  if (totalPages <= 1) {
    return null;
  }

  return (
    <nav className={cn('flex items-center justify-center', className)}>
      <Pagination>
        <PaginationContent>
          <PaginationItem>
            <PaginationPrevious
              href={createPageURL(currentPage - 1)}
              aria-disabled={currentPage <= 1}
              className={currentPage <= 1 ? "pointer-events-none opacity-50" : undefined}
            />
          </PaginationItem>

          {paginationItems.map((page, index) => (
            <PaginationItem key={`${page}-${index}`}>
              {page === '...' ? (
                <PaginationEllipsis />
              ) : (
                <PaginationLink
                  href={createPageURL(page as number)}
                  isActive={currentPage === page}
                >
                  {page}
                </PaginationLink>
              )}
            </PaginationItem>
          ))}

          <PaginationItem>
            <PaginationNext
              href={createPageURL(currentPage + 1)}
              aria-disabled={currentPage >= totalPages}
               className={currentPage >= totalPages ? "pointer-events-none opacity-50" : undefined}
            />
          </PaginationItem>
        </PaginationContent>
      </Pagination>
    </nav>
  );
}

// Since shadcn/ui doesn't ship with pagination components by default, we'll add them here.
// These are simplified versions based on the shadcn/ui style.
const PaginationPrimitive = ({
  className,
  ...props
}: ComponentProps<'nav'>) => (
  <nav
    role="navigation"
    aria-label="pagination"
    className={cn('mx-auto flex w-full justify-center', className)}
    {...props}
  />
);

const PaginationContentPrimitive = forwardRef<
  HTMLUListElement,
  ComponentProps<'ul'>
>(({ className, ...props }, ref) => (
  <ul
    ref={ref}
    className={cn('flex flex-row items-center gap-1', className)}
    {...props}
  />
));
PaginationContentPrimitive.displayName = 'PaginationContent';

const PaginationItemPrimitive = forwardRef<
  HTMLLIElement,
  ComponentProps<'li'>
>(({ className, ...props }, ref) => (
  <li ref={ref} className={cn('', className)} {...props} />
));
PaginationItemPrimitive.displayName = 'PaginationItem';

type PaginationLinkProps = {
  isActive?: boolean;
} & ComponentProps<typeof Link>;

const PaginationLinkPrimitive = ({
  className,
  isActive,
  ...props
}: PaginationLinkProps) => (
  <Link
    aria-current={isActive ? 'page' : undefined}
    className={cn(
      'flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2',
      'h-10 w-10',
      isActive
        ? 'border border-input bg-background shadow-sm'
        : 'hover:bg-accent hover:text-accent-foreground',
      className
    )}
    {...props}
  />
);
PaginationLinkPrimitive.displayName = 'PaginationLink';

const PaginationPreviousPrimitive = ({
  className,
  ...props
}: ComponentProps<typeof PaginationLinkPrimitive>) => (
  <PaginationLinkPrimitive
    aria-label="Go to previous page"
    className={cn('gap-1 pl-2.5', className)}
    {...props}
  >
    <span>Previous</span>
  </PaginationLinkPrimitive>
);

const PaginationNextPrimitive = ({
  className,
  ...props
}: ComponentProps<typeof PaginationLinkPrimitive>) => (
  <PaginationLinkPrimitive
    aria-label="Go to next page"
    className={cn('gap-1 pr-2.5', className)}
    {...props}
  >
    <span>Next</span>
  </PaginationLinkPrimitive>
);

const PaginationEllipsisPrimitive = ({
  className,
  ...props
}: ComponentProps<'span'>) => (
  <span
    aria-hidden
    className={cn('flex h-9 w-9 items-center justify-center', className)}
    {...props}
  >
    ...
    <span className="sr-only">More pages</span>
  </span>
);

// We re-export them under the original names to be used in PaginationControls
const ui = {
  Pagination: PaginationPrimitive,
  PaginationContent: PaginationContentPrimitive,
  PaginationEllipsis: PaginationEllipsisPrimitive,
  PaginationItem: PaginationItemPrimitive,
  PaginationLink: PaginationLinkPrimitive,
  PaginationNext: PaginationNextPrimitive,
  PaginationPrevious: PaginationPreviousPrimitive,
};
export {
  ui as PaginationUI,
  PaginationPrimitive as Pagination,
  PaginationContentPrimitive as PaginationContent,
  PaginationEllipsisPrimitive as PaginationEllipsis,
  PaginationItemPrimitive as PaginationItem,
  PaginationLinkPrimitive as PaginationLink,
  PaginationNextPrimitive as PaginationNext,
  PaginationPreviousPrimitive as PaginationPrevious,
};
